'use client';
import { useCallback, useEffect } from 'react';
import ReactFlow, {
  Controls,
  Background,
  MiniMap,
  ReactFlowProvider,
  Node,
  useReactFlow,
} from 'reactflow';
import useStore from '@/store/useStore';
import Sidebar from '@/components/Sidebar';
import Header from '@/components/Header';
import TextNode from '@/components/nodes/TextNode';
import ImageNode from '@/components/nodes/ImageNode';
import LLMNode from '@/components/nodes/LLMNode';
import { defaultNodes, defaultEdges } from '@/lib/defaultWorkflow';

import 'reactflow/dist/style.css';

const nodeTypes = {
  textNode: TextNode,
  imageNode: ImageNode,
  llmNode: LLMNode,
};

function Flow() {
  const { nodes, edges, onNodesChange, onEdgesChange, onConnect, addNode, setNodes, setEdges } = useStore();
  const { screenToFlowPosition } = useReactFlow();

  useEffect(() => {
     // Load default workflow if empty
     // Check if we have nodes in store (could be empty array)
     // To strictly load the requirement every time or just once? "Pre-build... that loads by default".
     // I'll check if edges/nodes are empty.
     if (nodes.length === 0) {
         setNodes(defaultNodes);
         setEdges(defaultEdges);
     }
     // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  
  const onDragOver = useCallback((event: React.DragEvent) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  }, []);

  const onDrop = useCallback(
    (event: React.DragEvent) => {
      event.preventDefault();

      const type = event.dataTransfer.getData('application/reactflow');
      if (typeof type === 'undefined' || !type) {
        return;
      }
      
      const position = screenToFlowPosition({
        x: event.clientX,
        y: event.clientY,
      });
      
      const newNode: Node = {
        id: crypto.randomUUID(),
        type,
        position,
        data: {}, 
      };

      if (type === 'llmNode') newNode.data = { label: 'New Model', model: 'gemini-2.0-flash' };
      if (type === 'textNode') newNode.data = { label: 'Text', text: '' };
      if (type === 'imageNode') newNode.data = { label: 'Image', imageData: null };

      addNode(newNode);
    },
    [screenToFlowPosition, addNode]
  );
  
  return (
      <div className="flex flex-col w-full h-screen overflow-hidden bg-slate-50 dark:bg-[#020617]">
        <Header />
        <div className="flex flex-1 overflow-hidden">
            <Sidebar />
            <div className="flex-1 h-full relative">
                <ReactFlow
                    nodes={nodes}
                    edges={edges}
                    onNodesChange={onNodesChange}
                    onEdgesChange={onEdgesChange}
                    onConnect={onConnect}
                    nodeTypes={nodeTypes}
                    onDragOver={onDragOver}
                    onDrop={onDrop}
                    fitView
                    className="bg-slate-50 dark:bg-[#020617]"
                    minZoom={0.1}
                    maxZoom={4}
                >
                    <Background color="#334155" gap={24} size={1} className="opacity-[0.15]" />
                    <Controls className="!bg-white dark:!bg-[#0f172a] !border-slate-200 dark:!border-slate-800 !fill-current !text-slate-600 dark:!text-slate-300 !rounded-lg !shadow-xl !p-1" />
                    <MiniMap 
                        className="!bg-white dark:!bg-[#0f172a] !border-slate-200 dark:!border-slate-800 rounded-xl overflow-hidden shadow-xl" 
                        nodeColor={(n) => {
                            if (n.type === 'llmNode') return '#7c3aed';
                            if (n.type === 'imageNode') return '#ec4899';
                            if (n.type === 'textNode') return '#3b82f6';
                            return '#64748b';
                        }}
                        maskColor="rgba(0, 0, 0, 0.2)"
                    />
                </ReactFlow>
            </div>
        </div>
      </div>
  );
}

export default function Page() {
    return (
        <ReactFlowProvider>
            <Flow />
        </ReactFlowProvider>
    );
}
