import React from 'react';
import { Share2, ChevronDown, Zap, Menu } from 'lucide-react';

const Header = () => {
  return (
    <header className="h-14 border-b border-slate-200 dark:border-slate-800 bg-white dark:bg-[#020617] flex items-center justify-between px-4 z-20 relative">
      <div className="flex items-center gap-4">
        {/* Mobile menu trigger could go here */}
        <div className="flex items-center gap-2">
            <div className="w-8 h-8 bg-primary/10 rounded-lg flex items-center justify-center text-primary font-bold text-lg">
                G
            </div>
            <div className="h-8 w-[1px] bg-slate-200 dark:bg-slate-800 mx-2"></div>
            <div className="flex flex-col">
                <input 
                    type="text" 
                    defaultValue="Untitled Project" 
                    className="bg-transparent text-sm font-semibold text-slate-700 dark:text-slate-200 focus:outline-none focus:ring-2 focus:ring-primary/50 rounded px-1 -ml-1"
                />
            </div>
        </div>
      </div>

      <div className="flex items-center gap-3">
        <div className="flex items-center gap-2 px-3 py-1.5 bg-slate-100 dark:bg-slate-900 rounded-full border border-slate-200 dark:border-slate-800">
            <Zap size={14} className="text-yellow-500 fill-yellow-500" />
            <span className="text-xs font-medium text-slate-600 dark:text-slate-300">150 credits</span>
        </div>

        <button className="flex items-center gap-2 px-3 py-1.5 text-sm font-medium text-slate-600 dark:text-slate-300 hover:bg-slate-100 dark:hover:bg-slate-800 rounded-lg transition-colors">
            Tasks
            <ChevronDown size={14} />
        </button>

        <button className="flex items-center gap-2 px-4 py-1.5 bg-white dark:bg-slate-100 text-slate-900 hover:bg-slate-50 transition-colors rounded-lg text-sm font-semibold shadow-sm">
            <Share2 size={16} />
            Share
        </button>
      </div>
    </header>
  );
};

export default Header;
